import { Link, useLoaderData } from "remix"

export const loader = () => {
    return [
        {
            slug: "my-first-post",
            title: "My first post"
        },
        {
            slug: "90s-mixtape",
            title: "A Mixtape I made Just For you"
        }
    ]
}

export default function Posts() {
    const posts = useLoaderData();
    console.log(posts);
    return(
        <div>
            Posts
            <ul>
                {posts.map(post => (
                    <li key={post.slug}>
                        <Link to={post.slug}>{post.title}</Link>
                    </li>
                ))}
            </ul>
        </div>
    )
}